import axios from 'axios';

export const getAllPost = () => {
  return axios({
    method: 'get',
    url: "https://us-central1-my-github-page-f7626.cloudfunctions.net/app/blogs",
  })
}

export const getPostById = ({blogId}) => {
  return axios({
    method: 'get',
    url: `https://us-central1-my-github-page-f7626.cloudfunctions.net/app/blogs/${blogId}`,
  })
}

export const updateBlog = ({blogId,data}) => {
  return axios( {
    method: 'patch',
    url: `https://us-central1-my-github-page-f7626.cloudfunctions.net/app/blogs/${blogId}`,
    data:data
  })
}


export const postNewBlog = ({data}) => {
  return axios( {
    method: 'post',
    url: `https://us-central1-my-github-page-f7626.cloudfunctions.net/app/blogs`,
    data:data
  })
}