import React from "react";
import { BootstrapCss } from "./style";
import "./assets/vendor/bootstrap/css/bootstrap.min.css";
import "./assets/vendor/bootstrap-icons/bootstrap-icons.css";
import "./assets/vendor/boxicons/css/boxicons.min.css";
import "./assets/vendor/glightbox/css/glightbox.min.css";
import "./assets/vendor/remixicon/remixicon.css";
import "./assets/vendor/swiper/swiper-bundle.min.css";
import { useState, useEffect } from "react";
import {
  Link,
  BrowserRouter as Router,
  Switch,
  Route,
  useLocation,
  useParams,
} from "react-router-dom";
import { getAllPost, getPostById, postNewBlog, updateBlog } from "./api/blog";
import { loginUser } from "./api/user";

function App() {
  const [onClickedIndex, setOnClickIndex] = useState(0);
  const nav_items = ["Home", "About", "Blog", "Contact", "Login"];
  const [blogList, setBlogList] = useState([]);
  const [userData, setUserData] = useState({});

  useEffect(() => {
    fetchBlogs();
  }, []);

  const fetchBlogs = () => {
    getAllPost().then((res) => {
      setBlogList(res.data);
    });
  };

  const NavItem = ({ title, active, handleOnClick }) => {
    return (
      <li>
        <Link to={title == "Home" ? "/" : `/${title}`}>
          <a
            style={{ cursor: "pointer" }}
            className={
              active ? "nav-link scrollto active" : "nav-link scrollto"
            }
            onClick={handleOnClick}
          >
            {title}
          </a>
        </Link>
      </li>
    );
  };

  const Main = () => {
    const { id } = useParams();
    return (
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route path="/About">
          <About />
        </Route>
        <Route
          path="/Blog/:blogId"
          render={({ match }) => <SingleBlog blogId={match.params.blogId} />}
        />
        <Route path="/Blog">
          <Blog />
        </Route>

        <Route path="/Contact">
          <Contact />
        </Route>
        <Route path="/Login">
          <Login />
        </Route>
      </Switch>
    );
  };

  const NavList = ({ list }) => {
    return (
      <ul>
        {list &&
          list.map((item, index) => (
            <NavItem
              title={item}
              active={index == onClickedIndex}
              handleOnClick={() => setOnClickIndex(index)}
            />
          ))}
      </ul>
    );
  };

  const Login = () => {
    const [userName, setUserName] = useState("");
    
    const handleLoginUser = () => {
      const data = {
        userName: userName
      }
      loginUser({data}).then(res => {
        setUserData(res.data.data[0]);
      })
    }

    return (
      <main id="main">
        <section id="breadcrumbs" className="breadcrumbs">
          <div className="container">
            <ol>
              <li>
                <a href="index.html">Home</a>
              </li>
              <li>Blog</li>
            </ol>
          </div>
        </section>
        <section id="blog" className="blog">
          <label>username</label>
          <input value={userName} onChange={(evt) => setUserName(evt.currentTarget.value) }/>  
          <button onClick={() => handleLoginUser() }>Login</button>
        </section>
        
      </main>
    )
  }

  const Home = () => {
    return (
      <section id="hero">
        <div className="hero-container">
          <h3>
            Welcome to <strong>Peiting's Website</strong>
          </h3>
          <h1>I am a believer in knowledge</h1>
          <h2>
            I believe knowledge is boudless , and it is never too old to learn
          </h2>
          <a href="#about" className="btn-get-started scrollto">
            Get Started
          </a>
        </div>
      </section>
    );
  };

  const About = () => {
    return <>about</>;
  };

  const EditBlogPage = ({closeBlogDetail, blogId}) => {
    const [topic, setTopic] = useState('');
    const [author, setAuthor] = useState('');
    const [content, setContent] = useState('');

    const handleSaveBlog = () => {
      const data = {
        topic: topic,
        author: author,
        content: content,
        uid: userData.id
      }
      updateBlog({blogId, data}).then(res => {
        console.log(res)
        if(res.data.status == 'success'){
          closeBlogDetail(); 
        }
      });
      
    }
    return (
      <main id="main">
        <section id="breadcrumbs" className="breadcrumbs">
          <div className="container">
            <ol>
              <li>
                <a href="index.html">Home</a>
              </li>
              <li>Blog</li>
            </ol>
          </div>
        </section>
        <section id="blog" className="blog">
          <div className="container" data-aos="fade-up">
            <label>blog topic</label>
            <input value={topic} onChange={evt => setTopic(evt.currentTarget.value)}/>
            <label>blog author</label>
            <input value={author} onChange={evt => setAuthor(evt.currentTarget.value)}/>
            <label>blog Content</label>
            <textarea value={content} onChange={evt => setContent(evt.currentTarget.value)}></textarea>
            <button onClick={() => {handleSaveBlog(); }}>Save</button>
          </div>
        </section>
      </main>
    )
  }

  const PreviewBlogPage = ({blogId, openBlogDetail, fetchSingleBlog, selectedBlog}) => {
    const [permission, setPermission] = useState(false);
    useEffect(() => {
      console.log(selectedBlog)
      fetchSingleBlog(blogId);
    }, []);

    useEffect(() => {
      //check permission for editing
      // let blog_list = userData && userData.blogs ? userData.blogs.split(',') : [];
      //check blogId is in the list
      if(selectedBlog.uid == userData.id)
        setPermission(true);
      else
        setPermission(false);
    },[selectedBlog])

    return (
      <main id="main">
        {/* <!-- ======= Breadcrumbs ======= --> */}
        <section id="breadcrumbs" className="breadcrumbs">
          <div className="container">
            <ol>
              <li>
                <a href="index.html">Home</a>
              </li>
              <li>Blog</li>
            </ol>
            <h2>{selectedBlog.topic}</h2>
          </div>
        </section>
        {/* <!-- End Breadcrumbs --> */}

        {/* <!-- ======= Blog Section ======= --> */}
        <section id="blog" className="blog">
          <div className="container" data-aos="fade-up">
            <div className="row">
              <article className="entry entry-single">
                <div className="entry-img">
                  <img
                    src="assets/img/blog/blog-1.jpg"
                    alt=""
                    className="img-fluid"
                  />
                </div>

                <h2 className="entry-title">
                  <a href="blog-single.html">{selectedBlog.topic}</a>
                </h2>
                {permission &&
                  <button onClick={() => {openBlogDetail()}}>edit</button>
                }
                <div className="entry-meta">
                  <ul>
                    <li className="d-flex align-items-center">
                      <i className="bi bi-person"></i>{" "}
                      <a href="blog-single.html">{selectedBlog.author}</a>
                    </li>
                    <li className="d-flex align-items-center">
                      <i className="bi bi-clock"></i>{" "}
                      <a href="blog-single.html">
                        <time datetime="2020-01-01">Jan 1, 2020</time>
                      </a>
                    </li>
                    {/* <li className="d-flex align-items-center"><i className="bi bi-chat-dots"></i> <a href="blog-single.html">12 Comments</a></li> */}
                  </ul>
                </div>

                <div className="entry-content">{selectedBlog.content}</div>
              </article>
            </div>
          </div>
        </section>
        {/* <!-- End Blog Section --> */}
      </main>
    )
  }

  const SingleBlog = ({ blogId }) => {
    const [openDetail, setOpenDetail] = useState(false);
    const [selectedBlog, setSelectedBlog] = useState({});

    const fetchSingleBlog = (blogId) => {
      getPostById({ blogId }).then((res) => {
        setSelectedBlog(res.data.data);
      });
    };

    const openBlogDetail = () => {
      setOpenDetail(true);
    }
    const closeBlogDetail = () => {
      setOpenDetail(false);
    }

    return (
      <>
      { !openDetail ? 
        <PreviewBlogPage 
          openBlogDetail={openBlogDetail}
          blogId={blogId}
          fetchSingleBlog={fetchSingleBlog}
          selectedBlog={selectedBlog}/>
      : 
        <EditBlogPage 
          blogId={blogId}
          closeBlogDetail={closeBlogDetail}/>  
      }
      </>
    );
  };

  const BlogList = () => {
    return (
      <>
        <div className="col-lg-8 entries">
          {blogList.map((blog) => (
            <article className="entry">
              <div className="entry-img">
                <img src="./img/blog/blog-1.jpg" alt="" className="img-fluid" />
              </div>

              <h2 className="entry-title">
                <a>{blog.topic}</a>
              </h2>

              <div className="entry-meta">
                <ul>
                  <li className="d-flex align-items-center">
                    <i className="bi bi-person"></i> <a>{blog.author}</a>
                  </li>
                  <li className="d-flex align-items-center">
                    <i className="bi bi-clock"></i>{" "}
                    <a>
                      <time datetime="2020-01-01">Jan 1, 2020</time>
                    </a>
                  </li>
                  {/* <li className="d-flex align-items-center"><i className="bi bi-chat-dots"></i> <a  >12 Comments</a></li> */}
                </ul>
              </div>

              <div className="entry-content">
                <p>{blog.content}</p>
                <div className="read-more">
                  <Link to={`/Blog/${blog.id}`}>
                    <a>
                      Read More
                    </a>
                  </Link>
                </div>
              </div>
            </article>
          ))}

          <div className="blog-pagination">
            <ul className="justify-content-center">
              <li>
                <a href="#">1</a>
              </li>
              <li className="active">
                <a href="#">2</a>
              </li>
              <li>
                <a href="#">3</a>
              </li>
            </ul>
          </div>
        </div>
        {/* <!-- End blog entries list --> */}

        <div className="col-lg-4">
          <div className="sidebar">
            <h3 className="sidebar-title">Search</h3>
            <div className="sidebar-item search-form">
              <form action="">
                <input type="text" />
                <button type="submit">
                  <i className="bi bi-search"></i>
                </button>
              </form>
            </div>
            {/* <!-- End sidebar search formn--> */}

            <h3 className="sidebar-title">Categories</h3>
            <div className="sidebar-item categories">
              <ul>
                <li>
                  <a href="#">
                    General <span>(25)</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    Lifestyle <span>(12)</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    Travel <span>(5)</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    Design <span>(22)</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    Creative <span>(8)</span>
                  </a>
                </li>
                <li>
                  <a href="#">
                    Educaion <span>(14)</span>
                  </a>
                </li>
              </ul>
            </div>
            {/* <!-- End sidebar categories--> */}

            <h3 className="sidebar-title">Recent Posts</h3>
            <div className="sidebar-item recent-posts">
              <div className="post-item clearfix">
                <img src="assets/img/blog/blog-recent-1.jpg" alt="" />
                <h4>
                  <a>Nihil blanditiis at in nihil autem</a>
                </h4>
                <time datetime="2020-01-01">Jan 1, 2020</time>
              </div>

              <div className="post-item clearfix">
                <img src="assets/img/blog/blog-recent-2.jpg" alt="" />
                <h4>
                  <a>Quidem autem et impedit</a>
                </h4>
                <time datetime="2020-01-01">Jan 1, 2020</time>
              </div>

              <div className="post-item clearfix">
                <img src="assets/img/blog/blog-recent-3.jpg" alt="" />
                <h4>
                  <a>Id quia et et ut maxime similique occaecati ut</a>
                </h4>
                <time datetime="2020-01-01">Jan 1, 2020</time>
              </div>

              <div className="post-item clearfix">
                <img src="assets/img/blog/blog-recent-4.jpg" alt="" />
                <h4>
                  <a>Laborum corporis quo dara net para</a>
                </h4>
                <time datetime="2020-01-01">Jan 1, 2020</time>
              </div>

              <div className="post-item clearfix">
                <img src="assets/img/blog/blog-recent-5.jpg" alt="" />
                <h4>
                  <a>Et dolores corrupti quae illo quod dolor</a>
                </h4>
                <time datetime="2020-01-01">Jan 1, 2020</time>
              </div>
            </div>
            {/* <!-- End sidebar recent posts--> */}

            <h3 className="sidebar-title">Tags</h3>
            <div className="sidebar-item tags">
              <ul>
                <li>
                  <a href="#">App</a>
                </li>
                <li>
                  <a href="#">IT</a>
                </li>
                <li>
                  <a href="#">Business</a>
                </li>
                <li>
                  <a href="#">Mac</a>
                </li>
                <li>
                  <a href="#">Design</a>
                </li>
                <li>
                  <a href="#">Office</a>
                </li>
                <li>
                  <a href="#">Creative</a>
                </li>
                <li>
                  <a href="#">Studio</a>
                </li>
                <li>
                  <a href="#">Smart</a>
                </li>
                <li>
                  <a href="#">Tips</a>
                </li>
                <li>
                  <a href="#">Marketing</a>
                </li>
              </ul>
            </div>
            {/* <!-- End sidebar tags--> */}
          </div>
          {/* <!-- End sidebar --> */}
        </div>
        {/* <!-- End blog sidebar --> */}
      </>
    );
  };

  const Blog = () => {
    const handlePostNewBlog = () => {
      const data = {
        uid: userData.id
      }
      postNewBlog({data}).then(res => {
        console.log(res.data.data)
      })
    }

    return (
      <>
        <main id="main">
          {/* <!-- ======= Breadcrumbs ======= --> */}
          <section id="breadcrumbs" className="breadcrumbs">
            <div className="container">
              <ol>
                <li>
                  <a href="index.html">Home</a>
                </li>
                <li>Blog</li>
              </ol>
              <h2>Blog</h2>
              <button onClick={handlePostNewBlog}>Post Blog</button>
            </div>
          </section>
          {/* <!-- End Breadcrumbs --> */}

          {/* <!-- ======= Blog Section ======= --> */}
          <section id="blog" className="blog">
            <div className="container" data-aos="fade-up">
              <div className="row">
                <BlogList />
              </div>
            </div>
          </section>
          {/* <!-- End Blog Section --> */}
        </main>
      </>
    );
  };

  const Contact = () => {
    return <>contact</>;
  };

  return (
    <BootstrapCss>
      <body>
        <Router>
          <header
            id="header"
            className="fixed-top "
            style={{ backgroundColor: "#000" }}
          >
            <div className="container d-flex align-items-center justify-content-between">
              <h1 className="logo">
                <a href="index.html">Sun</a>
              </h1>
              <nav id="navbar" className="navbar">
                <NavList list={nav_items} />
                <i className="bi bi-list mobile-nav-toggle"></i>
              </nav>
              {/* <!-- .navbar --> */}
            </div>
          </header>
          {/* <!-- End Header --> */}
          <Main />
        </Router>
      </body>
    </BootstrapCss>
  );
}

export default App;
